@import "_var";
@import url('https://fonts.googleapis.com/css?family=Lato');
@font-face {
    font-family: "Gotham - Black"; /* Имя шрифта */
    src: url(../fonts/GothamBlack.otf); /* Путь к файлу со шрифтом */
   }
@font-face {
    font-family: "Open Sans"; /* Имя шрифта */
    src: url(../fonts/OpenSans-Regular.ttf); /* Путь к файлу со шрифтом */
   }
   

.header {

    .header_tittle {    
        width: 1166px;
        margin: $block_center;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    
        .logo{
            background-image: url('../images/logo.png');
            background-repeat: no-repeat;
            background-position: center center;
            height: 50px;
            width: 49px;
            padding-top: 22px;
            padding-bottom: 23px;

            
        }
        .navigation {
            padding-top: 30px;
            padding-bottom: 30px;
            padding-left: 30px;

            .header_menu {
                display: flex;

                .menu_hover {
                    text-decoration: none;
                    padding: 12px 21px;
                    color: #868686;
                    font-family: 'Lato', sans-serif;
                    font-size: 14px;
                    font-weight: 400;
                    text-transform: uppercase;
                }

                .menu_hover:hover {
                        background-color: #ffbb42;
                        border-radius: 2px;
                }
            }
        }
    }
}

.quote {
    background-color: #709dca;
    .quote_all {
        width: 1166px;
        margin: $block_center;
        padding-top: 110px;
        
        .quote_tittle{
            color: #ffffff;
            text-align: center;
            
            h1 {
                font-size: 35px;
                font-weight: 400;
                letter-spacing: -0.88px;
                text-transform: uppercase;
                font-family: 'Gotham - Black', sans-serif;
            }

            p{
                font-family: 'Open Sans', sans-serif;
                font-size: 16px;
                font-weight: 300;
                line-height: 24px;
                padding-bottom: 33px;
            }
        }

        .quote_input {
           
            padding-bottom: 67px;
            
            .form_quote{
                display: flex;
                flex-direction: row;
                justify-content: center;
            
            .form_input{
                color: #a9a9a9;
                font-family: 'Open Sans', sans-serif;
                font-size: 13px;
                font-weight: 400;
                background-image: url('../images/user.png');
                background-repeat: no-repeat;
                background-position-x: 15px;
                background-position-y: 15px;
                padding-right: 10px;

                input {
                    padding: 15px 41px;
                    width: 202px;
                    
                }
  
            }
            .form_input:last-child {
                padding-right: 0;
            }
            .submit {
                background-color: #ffbb42;
                border: 0px;
                padding: 15px 41px;
                color: #ffffff;
                font-family: "Open Sans";
                font-size: 14px;
                font-weight: 400;
                text-transform: uppercase;
            }
        }
    }
        .quote_image{
            background-image: url('../images/pc.png');
            background-repeat: no-repeat;
            background-position: center center;
            height: 344px;
            width: 648px;
            position: relative;
            top: 63px;
            

            .image_slide {
                width: 446px;
                height: 265px;
                padding-top: 18px;
                padding-left: 102px;
            }
        }

        .quote_image_select{
            padding-top: 24px;
            
        }
    }
}

.main {
    background-color: #fff;
    .main_all {
        width: 1166px;
        margin: $block_center;
        padding-top: 203px;
        
        .main_tittle{
            color: #394b50;
            text-align: center;
            
            h1 {
                font-size: 30px;
                font-weight: 900;
                letter-spacing: -0.75px;
                text-transform: uppercase;
                font-family: 'Gotham - Black', sans-serif;
            }

            p{
                font-family: 'Open Sans', sans-serif;
                font-size: 16px;
                font-weight: 400;
                line-height: 22px;
                padding-bottom: 33px;
                
            }
        }
    
        .main_container{
            display: flex;
            flex-wrap: wrap;
            padding-bottom: 155px;
        
            .container {
                max-width: 523px;
                

               .icon:before {
                content: '';
                background-image: url('../images/spritesheet.png'); 
                background-repeat: no-repeat;
                display: block;
            }

            .icon-1:before {
                    background: url('../images/spritesheet.png') no-repeat -5px -5px;
                    width: 45px;
                    height: 46px;
                    display: inline-block;
                }
            
                .icon-2:before{
                    width: 47px;
                    height: 48px;
                    background: url('../images/spritesheet.png') no-repeat  -60px -5px;
                    display: inline-block;
                }
                
                .icon-3:before {
                    width: 62px;
                    height: 44px;
                    background: url('../images/spritesheet.png') no-repeat  -5px -63px;
                    display: inline-block;
                }
                
                .icon-4:before {
                    width: 50px;
                    height: 51px;
                    background: url('../images/spritesheet.png') no-repeat -77px -63px;
                    display: inline-block;
                }
                
                .icon-5:before {
                    width: 47px;
                    height: 47px;
                    background: url('../images/spritesheet.png') no-repeat -117px -5px;
                    display: inline-block;
                }
                
                .icon-6:before {
                    width: 31px;
                    height: 55px;
                    background: url('../images/spritesheet.png') no-repeat -137px -62px;
                    display: inline-block;
                }
             }

                .container_tittle{
                    padding-bottom: 21px;
                    padding-left: 39px;
                    color: #394b50;
                    font-family: Lato;
                    font-size: 16px;
                    font-weight: 700;
                    line-height: 22px;
                    text-transform: uppercase;
                    display: inline-block;
                    vertical-align: top;

                }
                .container_text{
                    padding-bottom: 71px;
                    padding-left: 88px;
                    color: #7b7b7b;
                    font-family: 'Open Sans', sans-serif;
                    font-size: 13px;
                    font-weight: 400;
                    line-height: 22px;
                    display: inline-block;
                    vertical-align: top;
                }
            }
        }
      
}

